@import '../../global.scss';

.login {
    height: 100vh;


    background-image: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url("data:image/jpeg;base64,");
    display         : flex;
    flex-direction  : column;
    justify-content : center;
    background-size : cover;
    object-fit      : cover;
    align-items     : center;

    @include mobile {
        height: 640px;
        width : 360px;
    }

    @include mobile {
        height: 640px;
        width : 360px;

    }

    .logintitle {
        font-size  : 40px;
        font-family: cursive;
        text-align : center;

    }

    .noronoro {
        background-color: white;
        display         : flex;
        // align-items: center;
        justify-content : center;
        flex-direction  : column;
        height          : 400px;
        width           : 500px;

        @include mobile {
            height: 360px;
            width : 330px;
        }

        .loginform {
            display       : flex;
            flex-direction: column;
            margin-left   : 30px;

            @include mobile {
                margin-left: 5px;
            }

            .lael {
                margin-left    : 10px;
                margin-top     : 10px;
                display        : flex;
                align-items    : center;
                width          : 350px;
                justify-content: space-between;

                @include mobile {
                    margin-left: 5px;
                    margin-top : 5px;
                    width      : 250px;
                }

                label {
                    font-size  : 16px;
                    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
                    margin     : 10px;
                }

                input {

                    margin-left  : 20px;
                    height       : 40px;
                    width        : 220px;
                    border       : none;
                    font-size    : 14px;
                    border-bottom: 1px solid #555;

                    &:invalid~.reddevil {
                        display: block;
                    }

                    @include mobile {
                        margin-left: 5px;
                        width      : 200px;

                    }
                }


            }

            .klill {
                display       : flex;
                flex-direction: column;
                align-items   : center;

                @include mobile {}

                .op {
                    margin-top: 20px;
                    color     : red;
                    font-size : 15px;
                }

                span {
                    color    : green;
                    font-size: 17px;

                }

                .lol {
                    background-clip: red;
                    margin-top     : 10px;
                }

                button {
                    margin-top: 30px;

                    height          : 40px;
                    width           : 60px;
                    background-color: rgb(63, 204, 150);
                    color           : white;
                    padding         : 10px;
                    display         : flex;
                    flex-direction  : column;
                    border          : none;
                    border-radius   : 5px;
                    align-items     : center;
                    cursor          : pointer;

                }

                button.disabled,
                button[disabled] {
                    background-color: red;
                    cursor          : no-drop;
                }
            }


        }

    }

    .tio {
        margin-top     : 20px;
        // margin-left: 100px;
        height         : 60px;
        display        : flex;
        align-items    : center;
        justify-content: center;

        @include mobile {
            margin-left: 10px;
        }

        button {

            height             : 40px;
            // width           : 60px;
            margin-top         : 10px;
            margin-left        : 20px;
            background-color   : indianred;
            color              : white;
            padding            : 10px;
            // display         : flex;
            // flex-direction  : column;
            border             : none;
            border-radius      : 5px;
            // align-items     : center;
            cursor             : pointer;

        }

        button.disabled,button[disabled]{
            background-color: red;
            cursor: no-drop;
        }

        .regitration {
            height             : 40px;
            // width           : 60px;
            margin-top         : 10px;
            margin-left        : 20px;
            background-color   : rgb(49, 131, 60);
            color              : white;
            padding            : 10px;
            // display         : flex;
            // flex-direction  : column;
            border             : none;
            border-radius      : 5px;
            // align-items     : center;
            cursor             : pointer;
        }

        h5 {
            margin-top   : 10px;
            margin-left  : 620px;
            margin-bottom: 5px;
        }

    }




}